            <div class="float-start">
                {{ id }}
            </div>
        </div>
        <div class="card-body">
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

// eslint-disable-next-line
const sections = ref([])
// eslint-disable-next-line
const props = defineProps({
    id: { required: true, type: String },
    struct: { default: null },
    active: { default: false, type: Boolean }
})

onMounted(() => {
    console.log(split_section(props.struct))
})
// logic to split the settings into their sections

function split_section(list) {
    let next_section = list.slice(1).findIndex(is_separator) + 1
    if (next_section == 0) {
        return []
    } else {
        return [list.slice(0,next_section)].push(split_section(list.slice(next_section)))
    }
}
function is_separator(obj) {
    if (obj['component'] == "section") {
        return true
    } else {
        return false
    }
}
</script>

